import React from 'react';
import { Link } from 'gatsby';
import Layout from '../../components/layout';
import ThumbnailStudio from '../../images/thumbnail_studio-640x350.jpg';
import ThumbnailTrainer from '../../images/thumbnail_trainer-640x350.jpg';
import ThumbnailAccess from '../../images/thumbnail_access-640x350.jpg';

export default function About() {
  return (
    <Layout
      page='about'
      subtitle='ジム情報'
      subtitleEn='INFORMATION'
      breadcrumbs={[{ link: '/about/', label: 'ジム情報' }]}
    >
      <main className='l-main'>
        <div className='wp-wrap-post'>
          <div data-row-pc='3' data-row-sp='1'>
            {[
              {
                link: '/about/studio/',
                image: ThumbnailStudio,
                label: 'OXWMについて'
              },
              {
                link: '/about/trainer/',
                image: ThumbnailTrainer,
                label: 'トレーナー紹介'
              },
              {
                link: '/about/access/',
                image: ThumbnailAccess,
                label: 'アクセス'
              }
            ].map(({ link, image, label }) => (
              <Link key={link} to={link} className='c-panel c-panel--col'>
                <div className='c-panel__img'>
                  <figure>
                    <img src={image} alt={label} />
                  </figure>
                </div>
                <div className='c-panel__detail'>
                  <div className='c-panel__title'>{label}</div>
                </div>
              </Link>
            ))}
          </div>
        </div>
      </main>
    </Layout>
  );
}
